@import '../../../assets/styles/mixins/flex.scss';
@import '../../../assets/styles/typography.scss';

.swiper {
    height: 100%;
    background: transparent;
    width: calc(80vw);

    @media (max-width: 1312px) {
        width: 100% !important;
    }
}
  
.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: transparent;
  
    /* Center slide text vertically */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
  }
  
  .swiper-slide .hero__slide {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .swiper-pagination-bullet {
    width: 10px !important;
    height: 10px !important;
    border: 1px solid #1A1F36 !important;
    background:  white !important;
    opacity: 1 !important;
  }

  .swiper-pagination-bullet-active {
    border: 1px solid #1A1F36 !important;
    background:  #373868 !important;
  }

  .swiper-pagination {
    position: relative !important;
    width: 1050px !important;
    margin-bottom: 87px;
    margin-top: -50px;

    @media (max-width: 1312px) {
        width: 100% !important;
    }
  }

.hero {
    &__section {
        position: relative;
        // background: #FFF;
    }

    &__main {
        // border: 1px solid black;
        width: 100px;
        height: 200px;
    }

    &__container {
        width: 100%;
        position: relative;
        padding-top: 128px;
        padding-bottom: 0;
        overflow: visible;
        @include flex(column, center, flex-start, nowrap);
        z-index: 1;

        @media (max-width: 1100px) {
            padding-top: 40px;
        }

        @media (max-width: 500px) {
            align-items: flex-start;
        }

        @media (max-height: 550px) {
            height: 100%;
        }
    }

    &__icon {
        width: 40px;
        height: 40px;
    }

    &__bim {
        padding-top: 12px;
    }

    &__icons {
        @include flex(row, flex-start, center);
        gap: 30px;
        margin-bottom: 30px;

        color: #0F1334;
        font-family: var(--font-family-uni-sans);
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.4px;
    }

    &__know {
        font-size: 112px;
        line-height: 101px;
        letter-spacing: -5.6px;

        @media (max-width: 800px) {
            font-size: 74px;
            line-height: 61px;
        }
    }

    &__scada {
        font-size: 112px;
        line-height: 122px;
        letter-spacing: -5.6px;

        @media (max-width: 800px) {
            font-size: 74px;
            line-height: 75px;
        }
    }

    &__eagle-mobile {
        margin-bottom: 10px;    
    }

    &__icons-inner {
        @include flex(row, flex-start, center, nowrap);
        gap: 15px;

        .hero__icon-bim {
            margin-bottom: -2px;
        }
    }

    &__top {
        @include flex(row, null, null, nowrap);
        gap: 33px;
        margin-bottom: 80px;

        @media (max-width: 1100px) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__right {
        height: 544px;
        width: 864px;
        image-rendering: pixelated;
        // width: calc(50vw + 32px);

        @media (max-width: 1100px) {
            max-width: 864px;
            width: 100%;
            height: 100%;
        }
    }

    &__left {
        max-width: 510px;

        @media (max-width: 1100px) {
            max-width: 100%;
        }
    }

    &__switcher {
        width: 100%;
        @include flex(row, center, center);
        margin-bottom: 80px;
    }

    &__description {
        margin-bottom: 70px;
        text-align: left;
    }

    &__buttons {
        @include flex(row, flex-start);
        gap: 28px;
    }

    &__button {
        @include flex(column, center);
        font-family: var(--font-family-uni-sans);
        border-radius: 100px;
        text-align: center;
        font-family: Uni Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.32px;
        padding: 3px 14px 0;
        height: 36px;
        width: fit-content;
        cursor: pointer;
        transition: all linear .2s;

        &:hover {
            opacity: .3;
        }
    }

    &__button-blue {
        color: #FFF;
        background: #101541;
    }    

    &__slider {
        position: relative;
        min-width: 0;
        height: 342px;
        width: 100%;
        margin-bottom: 80px;

        @media (max-width: 500px) {
            width: 50%;
            margin-bottom: 30px;
        }
    }

    &__slide {
        width: 100%;
    }

    &__swiper {
        width: 500px;
    }

    &__partners {
        @include flex(row, space-between, flex-start, nowrap);
        width: 100%;
        margin-bottom: 109px;

        &-img {
            max-height: 60px;
            max-width: 240px;
            width: 100%;
            // image-rendering: pixelated;

            @media (max-width: 500px) {
                transform: scale(1.5);
            }
        }
        
        &-mobile {
            @include flex(column, flex-start, center);
            width: 100%;
            gap: 40px;

            @media (max-width: 800px) {
                margin-bottom: 80px;
            }
        }

        &-line {
            @include flex(row, center,center, nowrap);
            width: 100%;
            gap: 60px;
            height: 90px;
        }

        &-icon {
            max-width: 145px;
            width: 100%;
        }
    }

    &__title {
        width: 100%;
        text-align: left;
        font-family: var(--font-family-inter);
        color: black;
        font-weight: 700;
        font-size: 65px;
        line-height: 77px;
        letter-spacing: -0.65px;
        margin-bottom: 42px;

        @media (max-width: 1100px) {
            font-size: 55px;
            line-height: 67px;
        }

        @media (max-width: 500px) {
            font-size: 45px;
            line-height: 57px;
        }

        @media (max-width: 385px) {
            font-size: 35px;
            line-height: 47px;
        }

        @media (max-width: 347px) {
            max-width: 215px;
        }
    }

    @keyframes check{
        0% {

        }

        50% {

        }

        100% {
            
        }
    }

    &__info {
        position: relative;
        padding: 20px 25px;
        @include flex(column);
        border-radius: 8px;
        margin-bottom: 170px;
        background: white;
        box-shadow: 0px 6px 10px 0px rgba(0, 5, 50, 0.15);
        color: #111439;
        width: 100%;
        transition: .2s linear;
        overflow: hidden;

        &:hover .hero__info-title{
            transform: translateY(10px);
        }

        &:hover .hero__info-description{
            transform: translateY(0px);
        }

        &:hover .hero__info-description-bottom{
            visibility: visible;
            opacity: 1;
            transform: translateY(0);
        }

        &-title {
            transform: translateY(30px);
            font-family: var(--font-family-inter);
            font-size: 24px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            border-left: none;
            margin-bottom: 32px;
            z-index: 1;
            transition: .2s linear;

            @media (max-width: 1100px) {
                transform: translateY(10px);
            }
        }

        &-title-mobile {
            margin-bottom: 20px;
        }

        &-description {
            transform: translateY(30px);
            margin-bottom: 10px;
            color: #425466;
            font-family: var(--font-family-uni-sans);
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 28px;
            letter-spacing: 0.36px;
            z-index: 1;
            transition: .2s linear;

            @media (max-width: 1100px) {
                transform: translateY(0px);
            }
        }

        &-description-mobile {
            font-family: var(--font-family-uni-sans);
            font-size: 16px;
            font-weight: 400;
            line-height: normal;
            margin-bottom: 14px;
        }

        &-description-bottom {
            visibility: hidden;
            opacity: 0;
            // max-height: 0;
            color: #425466;
            font-family: var(--font-family-uni-sans);
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 28px;
            letter-spacing: 0.36px;
            z-index: 1;
            transform: translateY(56px);
            transition: .2s linear;

            @media (max-width: 1100px) {
                visibility: visible;
                opacity: 1;
                transform: translateY(0);
            }
        }

        &-img {
            position: absolute;
            right: 22px;
            bottom: -24px;
            z-index: 0;
            transition: bottom .2s linear, opacity .2s linear, filter .2s linear;
            filter: contrast(0%) opacity(10%);

            @media (max-width: 1100px) {
                bottom: 0px;
                filter: none;
            }
        }

        &:hover .hero__info-img{
            bottom: 0px;
            filter: none;
        }

        &-mobile {
            margin-bottom: 60px;
        }
    }
}

