@keyframes tr-one-animation {
    0% {
      left: 20px;
      rotate: 26deg;
    }

    30% {
      left: -500px;
      rotate: 26deg;
    }

    60% {
      left: -500px;
      rotate: 36deg;
    }

    80% {
      left: 20px;
      rotate: 36deg;
    }

    100% {
      left: 20px;
      rotate: 26deg;
    }
}

@keyframes tr-one-animation-mobile {
    0% {
      left: 20px;
      rotate: 26deg;
    }

    30% {
      left: -80px;
      rotate: 26deg;
    }

    60% {
      left: -80px;
      rotate: 36deg;
    }

    80% {
      left: -20px;
      rotate: 36deg;
    }

    100% {
      left: 20px;
      rotate: 26deg;
    }
}

.tr-one {
    position: absolute;
    top: -1140px;
    left: 20px;
    width: 200%;
    height: 200%;
    background: rgba(0, 71, 255, 0.20);
    box-shadow: 128px 0px 105px 0px rgba(0, 71, 255, 0.08) inset;
    filter: drop-shadow(-60px 0px 200px rgba(0, 166, 255, 0.30));
    animation: tr-one-animation 25s infinite ease-in-out;
    rotate: 26deg;
    z-index: 2;

    @media (max-width: 800px) {
        top: -1445px;
        width: 320%;
        animation: tr-one-animation-mobile 15s infinite ease-in-out;
    }

    @media (max-width: 430px) {
        top: -1445px;
        width: 320%;
    }
}

@keyframes animation-2 {
    0% {
      left: -100px;
      rotate: 120deg;
    }
  
    30% {
      left: -200px;
      rotate: 120deg;
    }
  
    60% {
      left: -500px;
      rotate: 100deg;
    }
  
    80% {
      left: -300px;
      rotate: 120deg;
    }
  
    100% {
      left: -100px;
    }
  }

  @keyframes animation-2-mobile {
    0% {
        top: -120px;
        left: 400px;
        height: 156%;
    }
  
    30% {
        top: -220px;
        left: 400px;
        height: 156%;
    }
  
    60% {
        top: -220px;
        left: 350px;
        height: 156%;
    }
  
    80% {
        top: -120px;
        left: 400px;
        height: 156%;
    }
  
    100% {
        top: -120px;
        left: 400px;
        height: 156%;
    }
  }  

.tr-two {
    top: 0;
    left: -100px;
    position: absolute;
    height: 200%;
    width: 200%;
    background: linear-gradient(-61deg, #3378FF 33.59%, #50DFFF 48.26%);
    /* filter: drop-shadow(-61px 4px 200px
  px
  rgba(0, 41, 255, 0.58)); */
    rotate: 0;
    z-index: 1;
