    @media (max-width: 800px) {
        top: -120px;
        left: 400px;
        height: 156%;
        animation: animation-2-mobile 30s infinite ease-in-out;
        background: linear-gradient(140deg, #3378FF 33.59%, #50DFFF 48.26%);
    }

    @media (max-width: 430px) {
        top: -120px;
        left: 400px;
